import React, { Component } from "react";
import { connect } from "react-redux";
import { setSerV } from "../redux/action";
import { Link } from "react-router-dom";

export class Nav extends Component
 {
  render()
   {
    return (
      <nav className="navbar navbar-expand-lg navbar-light bg-light fs-2">
        <div className="container-fluid">
          <a className="navbar-brand">Navbar</a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link
                  to="/home"
                  className="nav-link active"
                  aria-current="page"
                >
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link">Link</Link>
              </li>

              <li className="nav-item dropdown">
                <Link
                  className="nav-link dropdown-toggle"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Dropdown
                </Link>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li>
                    <Link className="dropdown-item">Action</Link>
                  </li>
                  <li>
                    <Link className="dropdown-item">Another action</Link>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li>
                    <Link className="dropdown-item">Something else here</Link>
                  </li>
                </ul>
              </li>

              <li className="nav-item">
                <Link
                  className="nav-link disabled"
                  tabindex="-1"
                  aria-disabled="true"
                >
                  Disabled
                </Link>
              </li>
              <li>
                  <Link to="/cart">
                         cart<sup>{this.props.cartVal.length}</sup>
                  </Link>
                
              </li>
            </ul>

            <form className="d-flex">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
                onChange={(e) => this.setState({ serV: e.target.value })}
              />
              <button
                className="btn btn-outline-success"
                type="submit"
                onClick={(e) => {
                  e.preventDefault();
                  console.log(this.state.serV);
                  this.props.serSend(this.state.serV);
                }}
              >
                Search
              </button>
            </form>
          </div>
          {console.log("hello", this.props.cartVal)}
        </div>
      </nav>
    );
  }
}

const mapStateToProps = (cstate) => ({
  cartVal: cstate.cartRedu.cart,
});

const mapDispatchToProps = (dispatch) => ({
  serSend: (payload) => dispatch(setSerV(payload)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Nav);
