import React, { useState } from 'react';
import styles from './index.module.css';

 const CardFiller = ({item,ValueFromChild} ) =>
  { 

    const{id,preview,name,description}=item;
     return ( 
        <div className={styles.divm} key={id} >
          
          <img className={styles.img} src={preview} alt="Card image cap" />
          <div className="card-body">
            <h5 className="card-title">{name}</h5>
            <p className="card-text">{description}</p>
           <button className="btn btn-primary"
              onClick={()=>ValueFromChild(item)}
            >
              Add To Cart
           </button>
          </div>

      </div>
    );
 
 }
  
 export default CardFiller;