import "./App.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import CardFiller from "./card";
import Nav from "./navbar";
import axios from "axios";
import { setCartData } from "./redux/action";
import { BrowserRouter, Link, Route, Switch } from "react-router-dom";
import Cart from "./cart";

export class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: [],
      SProduct: [],
      loader: true,
    };
    console.log("Mount Phase constructor");
  }

  componentDidMount() {
    console.log("mount Phase ComponentDidMount");
    axios
      .get("https://5d76bf96515d1a0014085cf9.mockapi.io/product/")
      .then((res) => {
        console.log(res.data);
        this.setState({ product: res.data, SProduct: res.data, loader: false });
      });
  }

  newP = () => {
    return this.state.SProduct.filter(({ name }) =>
      name.toLowerCase().includes(this.props.searchValue)
    );
  };

  handleAddToCart = (a) => {
    console.log(a);
    this.props.sendCart(a);
  };

  render() {
    return (
      <div>
        <BrowserRouter>
          <Nav></Nav>

          <Switch>

                <Route path="/cart" component={Cart}/>

                <div className="CardContainer d-flex flex-wrap">
                  {this.state.loader ? (
                    <h1>LOADING...</h1>
                  ) : this.props.searchValue == "" ? (
                    this.state.product.map((item) => (
                      <CardFiller
                        ValueFromChild={(d) => this.handleAddToCart(d)}
                        item={item}
                      />
                    ))
                  ) : (
                    this.newP().map((item) => (
                      <CardFiller
                        ValueFromChild={(d) => this.handleAddToCart(d)}
                        item={item}
                      />
                    ))
                  )}
                </div>;
               
          </Switch>
        </BrowserRouter>
      </div>
    );
  }
}

const mapStateToProps = (serstate) => ({
  searchValue: serstate.serReducers.serValue,
});

const mapDispatchToProps = (dispatch) => ({
  sendCart: (payload) => dispatch(setCartData(payload)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Home);
